body {
  background-color: white;
}

.border {
  background: #b8de6f;
  height: auto;
  width: 400px;
  display: block;
  margin: 40px auto;
  padding: 30px;
}
.container {
  display: block;
  margin: 40px auto;
  width: 360px;
}

.container__header__input {
  padding: 10px;
  font-size: 18px;
}

.container__header button {
  padding: 13px 26px;
  cursor: pointer;
}

.container__header__input:focus {
  outline: none;
}

.container__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  padding-top: 7px;
}

.todo__input {
  width: 290px;
  padding: 5px;
  font-size: 16px;
  margin-top: 4px;
}

input:disabled {
  background-color: white;
  border: none;
}

.list__div {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.remove__icon {
  height: 25px;
  width: 25px;
  margin-top: 8px;
  cursor: pointer;
  margin-left: 5px;
}

.edit__icon {
  height: 25px;
  width: 25px;
  margin-top: 8px;
  cursor: pointer;
  margin-left: 5px;
}

.save__icon {
  height: 25px;
  width: 25px;
  margin-top: 8px;
  cursor: pointer;
  margin-left: 5px;
}

.todo__input:focus {
  outline: none;
}

.hide {
  display: none !important;
}

.show {
  display: block !important;
}

.flex__container {
  display: flex;
}

.flex__container button {
  white-space: nowrap;
}
